export function useLang(ns: string) {
  const lang: any = ns === 'auto' ? detectBrowserLanguage() : ns
  const messages = getMessages(lang)
  if (lang === 'zh-CN') {
    return {
      lang: langCn,
      messages,
    }
  }

  return {
    lang: langEn,
    messages,
  }
}

