import React, { useRef } from 'react'
import styled from 'styled-components'
import { useOnClickOutside } from '../../hooks/useOnClickOutside'
import { ApplicationModal } from '../../state/application/actions'
import { useModalOpen, useToggleModal } from '../../state/application/hooks'
import { ExternalLink } from '../../theme'

const StyledMenu = styled.div`
  margin-left: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: none;
  text-align: left;
`

const MenuFlyout = styled.span`
  min-width: 16rem;
  background-color: white;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.01), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04),
    0px 24px 32px rgba(0, 0, 0, 0.01);
  border-radius: 4px;
  margin: 0.5rem;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  position: absolute;
  top: 2rem;
  right: -2rem;
  z-index: 100;

  ${({ theme }) => theme.mediaWidth.upToMedium`
    top: -17.25rem;
  `};
`

const MenuItem = styled(ExternalLink)`
  flex: 1;
  font-family: nasalization;
  padding: 0.5rem 1.5rem;
  color: ${({ theme }) => theme.text2};
  :hover {
    background-color: #f8f9fa;
    cursor: pointer;
    text-decoration: none;
  }
  > svg {
    margin-right: 8px;
  }
  :first-child {
    margin-top: 0.5rem;
  }
  :last-child {
    margin-bottom: 0.5rem;
  }
`

const CruSpan = styled.span`
  font-family: nasalization;
  color: #ffffff80;
  cursor: pointer;
  margin-right: 50px;
  &:hover {
    color: #eb761c;
  }
`

export default function Documents() {
  const node = useRef<HTMLDivElement>()
  const open = useModalOpen(ApplicationModal.MENU)
  const toggle = useToggleModal(ApplicationModal.MENU)
  useOnClickOutside(node, open ? toggle : undefined)

  return (
    // https://github.com/DefinitelyTyped/DefinitelyTyped/issues/30451
    <StyledMenu ref={node as any}>
      <CruSpan onClick={toggle}>Documents</CruSpan>
      {open && (
        <MenuFlyout>
          <MenuItem id="link" href="https://crust-data.oss-cn-shanghai.aliyuncs.com/crust-home/whitepapers/whitepaper_en.pdf">
            Technology Whitepaper
          </MenuItem>
          <MenuItem id="link" href="https://crust-data.oss-cn-shanghai.aliyuncs.com/crust-home/whitepapers/ecowhitepaper_en.pdf">
            Economy Whitepaper
          </MenuItem>
          <MenuItem id="link" href="https://github.com/crustio">
            Github
          </MenuItem>
        </MenuFlyout>
      )}
    </StyledMenu>
  )
}
